.button {
    cursor: pointer;
    font-weight: 600;
    line-height: 20px;
    color: #FFFFFF;

    background: #0080FF;
    border-radius: 3px;
    padding: 5px 25px;
    border: none;
}


.secondary {
    background: #E5F0FA;
    border: 1px solid #0066CC;
    border-radius: 3px;
    color: #0066CC;
}

